
import { EROFS } from 'constants'
import React, { ChangeEvent } from 'react'
import css from './DialogoNuevoQuiz.module.css'
interface IProps {
   closeDialog: () => void,
   addQuiz: (pregunta: string, respuestas: string[], correcta: string) => void
}
interface IState { respuestas: Array<string>, contador: number, correcta: string }
class DialogoNuevoQuiz extends React.Component<IProps, IState> {

   dialogo = React.createRef<HTMLDialogElement>()
   inputRespuesta = React.createRef<HTMLInputElement>()
   inputPregunta = React.createRef<HTMLInputElement>()
   state = {
      respuestas: [],
      contador: 1,
      correcta: '',
   }
   render = () => {
      return (
         <dialog className={css['dialogo']} ref={this.dialogo}>

            <h3 className={css['titulo']}>
               Creando nueva Pregunta
               <button onClick={this.cerrar} className={css['boton-cerrar']}>X</button>
            </h3> <hr/>
            <span >Escribe la pregunta: </span>
            <input ref={this.inputPregunta} type="text" placeholder='Aqui va el titulo de la pregunta...' />
            <div>
               <label>Selecciona la Respuesta correcta</label>
               <select onChange={this.manejadorSelect} name='correcta' value={this.state.correcta}>
                  {
                     this.state.respuestas.map((res, index) => (
                        <option key={index} >{res}</option>
                     ))
                  }
               </select>
            </div>
            <hr />
            <div className={css['li-respuestas']}>
               {this.state.respuestas.length == 0 ? 'NO HAY NINGUNA RESPUESTA TODAVIA...' :
                  (
                     this.state.respuestas.map((element, index) => <li key={index + element}>{element}</li>)
                  )}
            </div>
            <hr />
            <div>
               <input
                  type="text" placeholder='Ingresa nueva respuesta' ref={this.inputRespuesta}
                  onKeyPress={(e) => e.key == 'Enter' && this.addRespuesta()}
               />
               <button className={css['button']} onClick={this.addRespuesta} >Add Respuesta</button>
            </div>

            <hr />
            <button className={css['button']} onClick={this.finalizar} >Finalizar</button>

         </dialog>
      )
   }
   manejadorSelect = (event: React.ChangeEvent<HTMLSelectElement>) => {
      const value = event.currentTarget.value
      this.setState({ correcta: value })
   }
   addRespuesta = () => {
      let respuesta = this.inputRespuesta.current?.value
      if (respuesta != null) {
         this.setState(prevState => ({
            respuestas: [...prevState.respuestas, `${prevState.contador}. ${respuesta}`],
            contador: prevState.contador + 1
         }))
         if (this.inputRespuesta.current) this.inputRespuesta.current.value = ''
      }
   }
   cerrar = () => {
      this.dialogo.current?.close()
      this.props.closeDialog()
   }
   componentDidMount = () => this.dialogo.current?.showModal()

   finalizar = () => {
      const pregunta = this.inputPregunta.current?.value
      if (pregunta == null) throw new Error('element not found')
      this.props.addQuiz(pregunta, this.state.respuestas, this.state.correcta)
      this.cerrar()
   }

}
export default DialogoNuevoQuiz
