import { debug } from 'console'
import React from 'react'
import css from './QuizProperties.module.css'

interface IProps { openDialog: () => void }
interface IState { }

class QuizProperties extends React.Component<IProps, IState> {
   render = () => {
      return (
         <div className={css['tools-container']}>
            <label >Quiz Properties</label>
            <div className={css['contenido']}>
               <div className={css['botones-principales']}>
                  <button
                     className={css['botones']}
                     onClick={() => this.props.openDialog()}>
                     Nueva Pregunta
                        </button>
                  <button className={css['botones']}>Finalizar Edicion</button>

               </div>

               <hr />

            </div>
         </div>
      )
   }

}
export default QuizProperties