import React from 'react'
import Respuestas from '../Respuestas'
import css from './Quiz.module.css'
import image from './check.png'

interface props { pregunta: string, respuestas: string[], correcta: string }
interface state { respuestas: string[] }

class Quiz extends React.Component<props, state> {
   
   state = {
      respuestas: this.props.respuestas,
      
   }
   render = () => {
      
      return (
         <main className={css['quiz']}>
            <label className={css['numero-pregunta']}>PREGUNTA NUMERO { }</label>
            <label className={css['pregunta']}>{this.props.pregunta}</label>
            
            <div className={css['seleccion']}>
               <Respuestas respuestas={this.state.respuestas} />
            </div>
            
            <div className={css['respuesta-correcta']} >
               <img src={image} alt="check-icon"/>
               <span>La Respuesta correcta es: {this.props.correcta}</span>
            </div>
            
         </main>
      )
   }
}


export type { props };
export default Quiz