import React, { Component } from 'react'
import css from './ViewDefault.module.css'
interface props { setView: (viewType: string) => void }
interface state { }
class ViewDefault extends Component<props, state> {
   render = () => {
      console.log('estoy renderizando una vista por defecto')
      return (
         <main className={css['main']}>
            <h1>Quizzes App - Brandox</h1>
            <h2>Mis Quizzes</h2>
            <div className={css['my-quizzes']}>
               <hr />
               <li> QUIZ SOBRE LA PRIMERA GUERRA MUNDIAL</li>
               <li> QUIZ SOBRE DERIVADAS</li>
               <li> QUIZ SOBRE LA HISTORIA DE JAVASCRIPT</li>
         
         </div>
            <hr />
            <div>
               <button className={css['botones']} onClick={() => this.props.setView('viewadminquiz')}
               >Editar Quiz</button>
               <button className={css['botones']}>Probar Quiz</button>
            </div>

         </main>
      )
   }
   
}
export default ViewDefault