import React from 'react'
import DialogoNuevoQuiz from '../DialogoNuevoQuiz'
import Quiz, { quizType } from '../Quiz'
import css from './ViewAdminQuiz.module.css'
import QuizProperties from '../QuizProperties'
interface IProps { setView: (viewType: string) => void }

interface IState {
   mostrarDialogoNuevoQuiz: boolean,
   quizzes: Array<quizType>
}


class ViewAdminQuiz extends React.Component<IProps, IState> {
   static numeroPregunta: number = 0;
   state = {
      mostrarDialogoNuevoQuiz: false,
      quizzes: [{ pregunta: 'no hay na', respuestas: [''], correcta: '' }]
   }
   render = () => {
      return (
         <div className={css['parent-all-container']}>
            <h1>Quizzes App - Brandox</h1>
            <h3 className={css['h2']}>Administrar Quizzes</h3>

            <main className={css['main-container']}>
               <div className={css['quiz-container']}>
                  <div>quiz container</div>
                  {  // validacion para que no renderiza el primero
                     this.state.quizzes[this.state.quizzes.length - 1].pregunta !== 'no hay na' && (
                        this.state.quizzes.map((quiz, index) =>
                           <div key={quiz.correcta + index}>
                              <Quiz pregunta={quiz.pregunta} respuestas={quiz.respuestas} correcta={quiz.correcta} />
                           </div>

                        )
                     )
                  }
                  <Quiz
                     pregunta='Cual es estas es una tecnologia FrontEnd Web?'
                     respuestas={['Django', 'PHP', 'NodeJS', 'React']} correcta={'React'}
                  />
               </div>
               <QuizProperties openDialog = {this.openDialog}/>

            </main>
            <div className={css['center-button']}>
               <button
                  className={css['botones']} onClick={() => this.props.setView('viewdefault')}
               >Volver al Inicio</button>
            </div>
            {/* MOSTRANDO EL DIALGO CUANDO SE HAGA CLICK EN EL BOTON CORESPONDIENTE */}
            {this.state.mostrarDialogoNuevoQuiz &&
               <DialogoNuevoQuiz closeDialog={this.cerrarDialogo} addQuiz={this.addQuiz} />}

         </div>
      )
   }
   cerrarDialogo = () => {
      this.setState({ mostrarDialogoNuevoQuiz: false })
   }
   addQuiz = (pregunta: string, respuestas: string[], correcta: string) => {
      // condicional para la primera vez que el estado tiene una insercion de inicializacion
      if (this.state.quizzes[0].pregunta == 'no hay na') {
         this.setState(() => (
            { quizzes: [{ pregunta, respuestas, correcta }] }
         ))
      } else {
         this.setState((prevState) => (
            { quizzes: [...prevState.quizzes, { pregunta, respuestas, correcta }] }
         ))
      }
   }
   openDialog = () => {
      this.setState({ mostrarDialogoNuevoQuiz: true })
   }

}
export default ViewAdminQuiz