import React from 'react'
import css from './Background.module.css'
import ViewDefault from '../ViewDefault'
import ViewAdminQuiz from '../ViewAdminQuiz'
import { JsxElement } from 'typescript'

interface props {

}
interface state { currentView: JSX.Element }

class Background extends React.Component<props, state> {
   
   render = () => {
      return this.state.currentView
   }
   setView = (backgroundType: string) => {
      this.setState(() => {
         let view: JSX.Element = <></>
         switch (backgroundType.trim().toUpperCase()) {
            case 'VIEWADMINQUIZ': view = this.views.viewAdminQuiz; break
            case 'VIEWDEFAULT': view = this.views.viewDefault; break
            default: <div>VIEW NOT FOUND!!!</div>
         }
         return { currentView: view }
      })
   }
   views = {
      viewAdminQuiz: <ViewAdminQuiz setView={this.setView}/>,
      viewDefault: <ViewDefault setView={this.setView} />
   }
   state = {
      currentView: this.views.viewAdminQuiz
   }

}
export default Background