import React from 'react'
import css from './Respuestas.module.css'
interface IProps { respuestas: Array<string> }
interface IState { }
class Respuestas extends React.Component<IProps, IState> {

   render = () => {
      return <>{
         this.props.respuestas.map((res, index) => (
            <li key={index}
               className={css['li-respuesta']}
               onClick={this.manejadorClick}
            >
               <input
                  className={css['radio-button']}
                  type="radio" name="seleccion"
               />
               <span>{res}</span>
            </li>
         ))
      }</>;
   }
   manejadorClick = (event: React.MouseEvent) => {
      (event.currentTarget.children[0] as HTMLInputElement).checked = true
   }
}
export default Respuestas
